import React from 'react';
import { Outlet, Routes, Route } from 'react-router';
import { Insitutions } from './Insitutions';
import { AssetAccounts } from './AssetAccounts';
import { Assets } from './Assets';
import { StoreEvent } from './machines/store';
import { Interpreter } from 'xstate';
import { TopAppBar } from './components/TopAppBar';
import { Home } from './Home';
import { AddEditReport } from './AddReport';

type Props = {
  storeService: Interpreter<any, any, StoreEvent, any>;
};

export const AssetsAndReports: React.FC<Props> = ({
  storeService,
}) => {
  return (
    <div>
      <TopAppBar />
      {/* <Outlet /> */}
      <Routes>
        <Route
          path="institutions"
          element={<Insitutions storeService={storeService} />}
        />
        <Route path="assets" element={<Assets storeService={storeService} />} />
        <Route
          path="asset-accounts"
          element={<AssetAccounts storeService={storeService} />}
        />
        <Route element={<Home storeService={storeService} />} />
        <Route
          path="add-edit-report/:reportId"
          element={<AddEditReport storeService={storeService} />}
        />
        <Route
          path="add-edit-report"
          element={<AddEditReport storeService={storeService} />}
        />
      </Routes>
    </div>
  );
};
